<template>
  <div class="main">
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'app',
    mounted() {
      this.$router.push({
        name: 'Main'
      });
    }
  }

</script>

<style>
  * {
    margin: 0;
    padding: 0;
  }

  html,
  body,
  .main {
    width: 100%;
    height: 100%;
  }

  html,
  body {
    background: #eee;
    font-family: Lato, "Microsoft YaHei", sans-serif;
    font-size: 1.8vh;
    color: #eef3f0;
  }

  .swiper-pagination-bullet {
    width: 1vh;
    height: 1vh;
  }
</style>



