import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Main from '@/pages/Main'

Vue.use(Router)

const router = new Router({
  // html5history模式，返回真实路径需要后台配置
  // mode: 'history',
  routes: [
    {
      path: '/Main',
      name: 'Main',
      component: Main
    }
  ]
})

export default router


